
.media-list {
  padding: 50px 2vw;

  > li {
    flex-basis: 100%;
    min-width: 100%;
    margin-bottom: 6vw;
  }

  &__title {
    position: relative;
    padding-left: 1.2em;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: -12px;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background-color: $accent;
      z-index: -1;
    }
  }

  &__date {
    display: block;
    padding: 2.5vw;
    color: $gold;
    font-family: 'marion_regular';
    font-weight: 700;
    letter-spacing: .15em;
  }

  &__text {
    padding: 0 3vw;
    font-size: 16px;
    line-height: 1.9;
    letter-spacing: .065em;
  }

  @media screen and (max-width: $tablet-s) {
    &__date {
      text-align: right;
    }
  }

  @media screen and (min-width: $tablet-s) {
    display: flex;
    padding: 5vw 2vw;

    > li {
      flex-basis: 33.33%;
      min-width: 33.33%;
    }
  }
  @media screen and (min-width: $tablet) {
    &__text {
      font-size: 17px;
    }
  }
}

.action-block {
  margin-top: 4vw;
  text-align: center;

  &__logo {
    display: block;
    max-width: 70%;
    margin: 4vw auto;

    img {
      max-width: 100%;
    }
  }

  &__text {
    font-size: 22px;
    line-height: 1.3;

    p {
      margin-bottom: 4vw;
      padding: 0 4vw;
      font-family: 'marion_regular';
      letter-spacing: .07em;
    }

    b {
      font-weight: bold;
    }
  }

  &__secondary-text {
    margin-top: 2em;
    font-size: 17px;
    opacity: .8;
    letter-spacing: .07em;
  }

  @media screen and (min-width: $tablet-s) {

    &__text {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 36px;
      text-align: left;

      p {
        margin-bottom: 0;
      }
    }
  }
  @media screen and (min-width: $tablet) {
    &--has-logo {
      display: flex;
      justify-content: center;
      align-items: center;
    }

    &__logo {
      max-width: initial;
      margin: 0;
    }
  }
}
