.testimonial {
  overflow: hidden;

  &__photos {
    position: relative;
    padding: 4vw 90px;
  }

  &__text {
    padding-left: 5vw;

    .score,
    .main-title {
      opacity: 0;
      transition: opacity .8s .2s ease-in;
    }

    > p {
      font-size: 18px;
      line-height: 2;
      letter-spacing: .06em;
      opacity: 0;
      will-change: opacity;
      transition: all .6s .8s ease-in;

      &.light-text {
        opacity: 0;
        font-weight: 300;
      }
    }
  }

  &__galery {
    position: relative;
    display: flex;
    width: 100%;
    padding: 4vw 1vw;
    overflow-x: auto;

    > li {
      flex-basis: 13.66%;
      min-width: 80px;
      max-width: 13.66%;
      margin-left: 1.5%;
      margin-right: 1.5%;
      border-radius: 1px;
      box-shadow: 0 0 0 8px transparent;
      transition: transform .3s, box-shadow .3s;

      &:hover {
        transform: scale(1.4);
        box-shadow: 0 0 0 8px $white, 0 0 12px 8px rgba(0, 0, 0, 0.1);
      }
    }

    img {
      width: 100%;
      opacity: 0;
    }
  }

  @media screen and (max-width: $tablet-s - 1) {
    &__galery {
      padding-bottom: 20px;
    }
  }

  @media screen and (min-width: $tablet-s) {
    &__photos {
      float: left;
      width: 50%;
    }

    &__text {
      width: 50%;
      float: right;
    }
  }

  @media screen and (max-width: $mobile - 1) {
    &__photos {
      padding: 4vw;
    }
  }
}

.photo-block {
  position: relative;
  padding-bottom: 15%;

  &__img {
    position: relative;
    width: 54%;
    opacity: 0;
    box-shadow: 0 0 0 8px $white, 0 0 12px 8px rgba(0, 0, 0, 0.1);
    transition: opacity .8s ease-in;

    img {
      width: 100%;
    }

    &:before {
      content: 'before';
      position: absolute;
      bottom: 4%;
      left: 5%;
      font-weight: bold;
      font-size: 18px;
      color: $white;
    }

    &--after {
      position: absolute;
      bottom: 0;
      right: -50%;
      transition: opacity .4s .8s ease-in, right .4s .8s ease-in;

      &:before {
        content: 'after';
      }
    }
  }
}

.slick-active {
  .photo-block {
    &__img {
      opacity: 1;

      &--after {
        right: 0;
      }
    }
  }

  .testimonial {
    &__text {
      .score,
      .main-title {
        opacity: 1;
      }

      > p {
        opacity: 1;

        &.light-text {
          opacity: .6;
        }
      }
    }

    &__galery {
      img {
        opacity: 1;
      }
    }
  }
}

.testimonials {
  position: relative;
}

.testimonials-nav {
  position: absolute;
  display: flex;
  justify-content: space-between;
  bottom: 40%;
  left: 0;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;

  &__prev,
  &__next {
    position: relative;
    width: 60px;
    height: 60px;
    background-color: transparent;
    border: none;
    border-radius: 50%;
    transition: background-color .3s;

    &:before {
      content: '';
      position: absolute;
      top: 15px;
      left: 20px;
      width: 30px;
      height: 30px;
      border-left: 1px solid $text-color;
      border-bottom: 1px solid $text-color;
      transform: rotateZ(45deg);
      transition: border-color .3s;
    }

    &:hover {
      background-color: $gold;

      &:before {
        border-color: $white;
      }
    }
  }

  &__next:before {
    left: 10px;
    transform: rotateZ(-135deg);
  }

  @media screen and (max-width: $mobile - 1) {
    bottom: 260px;
    &__prev,
    &__next {
      background-color: rgba(255, 255, 255, 0.6);
      transform: scale(.6);
    }
  }

  @media screen and (min-width: $tablet-s) {
    width: 50%;
    top: 30%;
    bottom: initial;
    padding-right: 0;
  }
}

.score {
  margin-bottom: 3vw;

  &__icon {
    width: 4.5%;
    max-width: 50px;
    max-height: 50px;
    margin: 0 .2%;
    color: $text-color;

    &--gold {
      color: $gold;
    }
  }
}
