.l-footer {
  padding-top: 10vw;
}

.footer-logo {
  margin: 3vw auto 5vw;
}

.footer-content {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__social {
    margin-bottom: 4vw;
  }

  &__nav {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 4vw;
    font-size: 16px;
    letter-spacing: .02em;

    a {
      display: block;
      position: relative;
      padding: .5em 1em;
      line-height: 1.7;
      text-decoration: none;
      color: $text-color;

      &:before {
        content: '';
        position: absolute;
        left: 0;
        bottom: 3px;
        height: 2px;
        width: 100%;
        background-color: $gold;
        transform: scale(0);
        transition: transform .3s;
      }

      &:hover:before {
        transform: scale(1);
      }
    }
  }

  @media screen and (min-width: $tablet-s) {
    padding-bottom: 50px;

    &__social {
      position: absolute;
      left: 0;
      bottom: 0;
      margin-bottom: 0;
    }

    &__nav {
      margin-bottom: 0;
    }

    &__payment {
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }

  @media screen and (min-width: $desktop) {
    padding-bottom: 0;
  }
}

.social-links {
  display: flex;

  &__icon {
    width: 30px;
    height: 30px;
    margin-left: 6px;
    margin-right: 6px;
    color: $gold;
    transition: color .3s;

    &:hover {
      color: $text-color;
    }
  }
}

.payment-list {
  display: flex;

  a {
    display: block;
    margin: 0 5px;
    transition: scale .3s;

    &:hover {
      transform: scale(.95);
    }

    &:active {
      opacity: .7;
    }
  }

  &__icon {
    width: 70px;
    height: 40px;
  }
}

.footer-copyright {
  margin: 8vw 0;
  font-size: 12px;
  text-align: center;
  letter-spacing: .05em;
}
