.how-works-bg {
  @media screen and (min-width: $tablet-s) {
    position: relative;
    margin-top: 7vw;
    background-image: linear-gradient($gray_1);
    background-size: 360px 100%;
    background-repeat: no-repeat;

    &:before {
      content: 'Veer';
      position: absolute;
      top: 4vw;
      left: 2vw;
      font-family: 'marion_regular';
      font-size: 12vw;
      opacity: .05;
    }
  }
  @media screen and (min-width: $containerMaxWidth) {
    background-size: calc(50% - 700px + 360px) 100%;
  }
}

.how-works-content-wrap {
  @media screen and (min-width: $tablet-s) {
    position: relative;
    margin-left: 350px;

    &:before {
      position: absolute;
      content: '';
      bottom: 0;
      left: -400px;
      width: 800px;
      height: 95%;
      background-position: 50% 0;
      background-size: contain;
      background-repeat: no-repeat;
      background-image: url('../img/how-to_bg.png');
    }
  }
}

.how-works-content {
  position: relative;
  padding: 6vw 7vw;
  overflow: hidden;

  > li {
    margin-bottom: 1em;
    padding-bottom: 3vw;
    padding-right: 3vw;
  }

  &__title {
    position: relative;
    margin-bottom: .6em;
    font-size: 16px;
    font-weight: bold;
    font-family: 'marion_regular';
    letter-spacing: .15em;
    z-index: 1;

    &:before {
      content: '';
      position: absolute;
      top: -35px;
      left: -35px;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background-color: $gold;
      background-image: url('../img/sea-icon.png');
      background-repeat: no-repeat;
      background-position: 50% 50%;
      z-index: -1;
    }
  }

  &__text {
    font-size: 16px;
    line-height: 2;
    letter-spacing: .05em;

  }

  @media screen and (max-width: $mobile) {
    padding: 9vw;

    &__title {
      &:before {
        top: -25px;
        transform: scale(.7);
      }
    }
  }

  @media screen and (min-width: $tablet-s) {
    background-color: rgba(255, 255, 255, 0.8);
  }

  @media screen and (min-width: $tablet) {
    > li {
      width: 50%;
      float: left;
    }

    &__title {
      margin-bottom: 1.2em;
    }
  }
}
