.tabs-nav {
  display: flex;
  width: 100%;
  margin: 7vw 0;

  > li {
    flex-grow: 1;
  }

  &__btn {
    position: relative;
    width: 100%;
    font-size: 16px;
    line-height: 4;
    font-weight: bold;
    border: none;
    background-color: transparent;
    transition: color .3s;
    font-family: 'marion_regular';
    letter-spacing: .13em;

    > span {
      display: inline-block;
      text-align: left;
    }

    &:before {
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 100%;
      height: 2px;
      background: $text-color;
      transition: background-color .3s;
    }

    &:focus,
    &:active {
      outline: none;
    }

    &:active {
      opacity: .7;
    }

    &:not(.active):hover {
      cursor: pointer;
      color: $gold;
    }

    &.active {
      &:before {
        background: $gold;
      }
    }
  }

  @media screen and (min-width: $tablet) {
    &__btn:before {
      top: 50%;
    }
  }
}

.tabs-items {
  position: relative;
  transition: height .4s ease-in;
}

.tabs-item {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  .float-content {
    position: relative;
    padding: 2vw;
    left: -100%;
    opacity: 0;
    transition: opacity .8s ease-in, left .3s ease-in;
  }

  &__title {
    position: relative;
    margin-top: 6vw;
    margin-bottom: 2vw;
    font-size: 40px;

    i {
      position: absolute;
      bottom: -.3em;
      right: 0;
      font-size: 16vw;
      font-weight: bold;
      color: $white;
      font-family: 'marion_regular';
      text-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
      z-index: -1;
    }
  }

  &__text {
    font-size: 18px;
    line-height: 1.78;
  }

  &.active {
    .float-content {
      left: 0;
      opacity: 1;
    }
  }

  @media screen and (max-width: $tablet-s - 1) {
    &__img {
      display: none;
    }
  }

  @media screen and (min-width: $tablet-s) {
    .float-content {
      position: absolute;
      top: 10vw;
      padding: 0;
      z-index: 1;
    }

    &__text {
      max-width: 40%;
    }

    &__title {
      i {
        right: initial;
        left: 0;
      }
    }

    &__img {
      float: right;
      width: 100%;
      opacity: 0;
      transition: opacity .8s ease-in;
    }

    &.active {
      .tabs-item__img {
        visibility: visible;
        opacity: 1;
      }
    }
  }
}
